@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: 'Raleway', sans-serif;
  background-color: lightgray;
}

.App {
  text-align: center;
  align-items: center;
  background-color: lightgray;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.accordion-button::after {
    height: 5rem !important;
    background-size: 1.4rem !important;
    background-image: url("https://www.iconbunny.com/icons/media/catalog/product/cache/2/thumbnail/600x/1b89f2fc96fc819c2a7e15c7e545e8a9/2/1/2103.11-cross-plus-icon-iconbunny.jpg") !important;
}
.accordion-button:not(.collapsed)::after {
    background-size: 1.2rem !important;
    background-image: url("https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTOOGF2YV2tkMnO2cePvFOIK_bq8PI7BvwdtZYvFyQCITi8bx3qyB2wjnc99zsnwlMt7wA&usqp=CAU") !important;
    -webkit-transform: none !important;
            transform: none !important;
}
.accordion-button:not(.collapsed) {
    color: black !important;
    background-color: white !important;
}
.accordion-button {
    padding-top: 0 !important;
    padding-bottom: 0 !important;
}
.accordion-button:focus {
    box-shadow: 0 0 0 0rem !important;
}
.accordion {
    padding: 0 !important;
}
.accordion-item {
    border: none !important;
    border-bottom: 1px solid rgba(0,0,0,.125) !important;
    border-radius: 0px !important;
}
.accordion-body {
    margin-left: 140px; 
    font-family: 'Raleway'; 
    font-size: 12px; 
    color: grey;
}
.tag{
    border: none !important; 
    border-bottom: 1px solid grey !important; 
    width: 20% !important; 
    margin-left: 140px !important; 
    border-radius: 0px !important; 
    margin-bottom: 15px !important; 
    padding: 0 !important; 
    font-family: Raleway !important; 
    font-size: 12px !important;
}
.tag:focus {
    outline:0px !important;
    -webkit-appearance:none;
    box-shadow: none !important;
    border-color: black !important;
}
.badge {
    margin-right: 3px; 
    color: black !important; 
    font-family: Raleway; 
    opacity: 0.7;
}


.search-form{
    border: none !important; 
    border-bottom: 1px solid lightgray !important; 
    border-radius: 0px !important; 
    padding-left: 0 !important; 
    padding-top: 10px !important;
    font-family: Raleway !important; 
}

.search-form:focus {
    outline:0px !important;
    -webkit-appearance:none;
    box-shadow: none !important;
    border-color: black !important;
}
.container {
    background-color: white; 
    width:60% !important; 
    height: 80vh; 
    overflow: scroll; 
    align-items: center; 
    box-shadow: 0.1rem 0.1rem 0.2rem 0.1rem grey;
    border-radius: 8px;
}
